
.updateText{
  position: absolute;
  width: 100%;

  text-align: center;
  z-index: 999;
  color:#F8CB0C;
  display: none;
}
.downloadText{
  position: absolute;
  width: 100%;
  color:#F8CB0C;
  text-align: center;
  z-index: 999;
}
.noneData{
  display: none;
  clear: both;
}
.wishBtn{
  position: absolute;
  z-index: 999;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  margin-top: 30px;
}
html{
  -ms-overflow-style:none;
  overflow:-moz-scrollbars-none;
}
.tost{
  position: absolute;
  top: 40%;
  color: #F8CB0C;
  font-size: 25px;
  opacity: 0;

  transform: translateX(-50%);
  -webkit-transform: translateX(-50%);
  left:-100%;
  transition:all  linear 1s ;
  -webkit-transition: all  linear 1s ;
  z-index: 100;
}
.clearfix:after,.clearfix:before{
  content: '';
  display: table;
}
.clearfix:after{
  clear: both;
}
/*html::-webkit-scrollbar{width:0px}*/
::-webkit-scrollbar {width: 0px;height: 1px;
}
::-webkit-scrollbar-thumb {border-radius: 5px;-webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);background: rgba(0, 0, 0, 0.2);
}



